:root {
  --navbar-height: 4em;
}

hr {
  width: min(12em, 30%);
  margin-block: 3em;
}

q {
  margin-left: auto;
  &.small {
    margin-left: 2em;
  }
}

.link-navbar,
.link-navbar[data-toggle="collapse"] {
  position: relative;
  width: fit-content;

  font-size: 1.6em;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: $white;
    position: absolute;
    top: 100%;
    left: 0;
    opacity: 0;
    transform: none;
  }
  &.active,
  &:hover,
  &[aria-current="page"] {
    &::after {
      content: "";
      opacity: 1;
      transform: none;
    }
  }
}

.form-control {
  padding-block: 0.5em;
}

.form-group {
  max-width: none;
  flex-direction: row;
  gap: clamp(2em, 5vw, 4em);
  padding-block: 0.5em;
}

.form-checkbox {
  &,
  &.big {
    label {
      padding-block: 0.5em;
      font-size: var(--fs-5);
    }
    input[type="checkbox"] {
      width: 1em;
      height: 1em;
      margin-right: 1em;
    }
  }
}

.breadcrumb {
  display: flex;
  align-items: center;

  .li {
    font-size: var(--fs-5);
  }
}

.breadcrumb__item {
  margin: 0 1em;
  position: relative;
  a {
    color: $black;
    &:hover {
      color: $black;
    }
  }
}

.breadcrumb__item:not(:last-child) {
  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(1em, 0px);
    height: 100%;
    width: 2px;
    background-color: $black;
    border-radius: 30px;
  }
  a {
    color: $black;
    &:hover {
      color: $black;
    }
  }
}

.navbar-main {
  --navbar-padding-inline: 2em;
  --navbar-padding-block: 0.5em;
  background: rgba($color: $white, $alpha: 0.1);
  box-shadow: 0px 6px 8px 3px rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(2.5px);
  padding-block: var(--navbar-padding-block);
  padding-inline: var(--navbar-padding-inline);

  > .navbar-group {
    gap: 1em;

    > .link-group {
      display: flex;
    }
    > .link {
      display: none;
    }
    > .navbar-search {
      display: flex;
      form {
        display: flex;
        align-items: flex-end;
        gap: 1em;
      }
    }
  }
}

.navbar-collapse {
  padding-top: 0;
  top: var(--navbar-height);
  width: 100%;
  background: transparent;
  box-shadow: none;
  backdrop-filter: blur(2.5px);

  menu,
  ul {
    li {
      margin-top: 1.2em;
    }
  }
  menu {
    margin-block: 0;
    padding-top: 5em;
  }
  ul {
    .image-group {
      display: flex;
    }
  }
  .link {
    white-space: break-spaces;
  }
  .navbar-collapse-cross {
    display: none;
  }
  .navbar-collapse-left {
    display: flex;
    width: 32%;
    background: rgba($color: #ffffff, $alpha: 0.1);
    .link-navbar {
      color: #364354;
      &::after {
        background-color: #364354;
      }
    }
    ul {
      padding-left: 0;
    }
  }
  .navbar-collapse-middle {
    display: flex;
    width: 28%;
    background: rgba(120, 155, 206, 0.77);
  }
  .navbar-collapse-right {
    width: 40%;
    display: flex;
    background: #364354;
  }
}

.swiper-pagination {
  bottom: 1.25em !important;
}

.swiper-pagination-bullet {
  width: 5em !important;
  height: 0.5em !important;
  margin-inline: 1em !important;
}

.swiper-button-prev,
.swiper-button-next {
  width: 1.5em;
  height: 2.5em;
  bottom: 1.25em;
}

.swiper-button-prev {
  left: 2.5em;
}

.swiper-button-next {
  right: 2.5em;
}

.swiper-per-view {
  // max-width: 90%;
  margin-bottom: 1em;
  .swiper-wrapper {
    padding-bottom: 9em;
  }
  .swiper-slide {
    --swiper-slide-number: 2;
    // --swiper-slide-gap: 60px;
  }
}

.comment {
  margin-top: 5em;
  > figure {
    top: -6em;
    width: 7em;
    height: 7em;
    border: 1.5em solid white;
  }
  > div {
    // padding: 10%;
    padding-top: 6em;
    > div {
      gap: 0.125em;
      span {
        width: 1.125em;
        height: 1.125em;
      }
    }
  }
}
