//     margin-bottom: 1.1em;
//   }
//   .btn {
//     align-self: flex-start;
//   }
// }
.item-blog {
  > div {
    margin-bottom: 1.1em;
  }
  .btn {
    align-self: flex-start;
  }
}

.grid-item-shop {
  .grid-item-shop__header {
    flex-direction: row;
    figure {
      aspect-ratio: 4/5;
    }
    .colors {
      grid-auto-flow: column;
      grid-template-rows: repeat(3, 2.6em);
      grid-template-columns: repeat(2, 2.6em);
      gap: 0;
      margin-top: 0;
    }
    span {
      display: block;
    }
  }

  .grid-item-shop__price {
    font-size: var(--fs-4);
    font-weight: $fw-4;
  }
}

.grid-container {
  grid-template-columns: repeat(auto-fit, minmax(0, 17em));
}
