.item-blog {
  display: flex;
  flex-direction: column;
  > div {
    padding: 1.5em;
    border: 2px solid $black;
    margin-bottom: 2em;
    flex-grow: 1;
  }
  .btn {
    // width: 80%;
    align-self: center;
  }
  .fs-1-25 {
    font-size: 1.25em;
  }
}

.grid-item-shop {
  display: flex;
  flex-direction: column;
  border: 2px solid $black;
  padding: 1.5em;
  .grid-item-shop__header {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    // max-height: 12em;
    figure {
      height: 100%;
      width: 100%;
      aspect-ratio: 4/3;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
    .colors {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      gap: 0.5em;
      height: fit-content;
      margin-top: 0.5em;
    }
    span {
      width: 100%;
      aspect-ratio: 1;
      background-color: $black;
      margin: 0;
      cursor: pointer;
      &[aria-label="beige"] {
        background-color: #fff3da;
      }
      &[aria-label="green"] {
        background-color: #bab654;
      }
      &[aria-label="red"] {
        background-color: #af4a00;
      }
      &[aria-label="blue"] {
        background-color: #5378ac;
      }
      &[aria-label="purple"] {
        background-color: #362646;
      }
      &[aria-label="dark-red"] {
        background-color: #620f41;
      }

      display: none;
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5) {
        display: block;
      }
    }
  }
  .grid-item-shop__buttons {
    display: flex;
    align-items: center;
    gap: 0.5em;
    .icon-cart-icon {
      font-size: 110%;
    }
  }
  .grid-item-shop__price {
    font-size: var(--fs-4);
    font-weight: $fw-7;
  }
}

.grid-container {
  width: 100%;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 5em;
  row-gap: 2.5em;
  justify-content: center;
  justify-content: flex-start;
  padding: 2.5em 1.5em;
}

@include sm {
  @import "main-sm";
}

@include lg {
  @import "main-lg";
}
