@import "variables";

// font sizes
@each $name, $size in $font-sizes-mobile {
  .fs-#{$name} {
    font-size: calc(
      round(calc($size / $font-size-base * 1em * 100)) / 100
    ) !important;
  }
}

@include sm {
  @each $name, $size in $font-sizes-desktop {
    .fs-#{$name} {
      font-size: calc(
        round(calc($size / $font-size-base * 1em * 100)) / 100
      ) !important;
    }
  }
}

// font weights
@each $name, $weight in $font-weights {
  .fw-#{$name} {
    font-weight: $weight !important;
  }
}

// colors
@each $name, $color in $colors {
  .bg-#{$name} {
    background-color: $color !important;
  }
  .fc-#{$name} {
    color: $color !important;
  }
}
